<template>
        <div class="edExContainer">
          <h3 class="edExHeader">Education and Experience</h3>
          <section class="allEducation">
            <h1>Education</h1>
          </section>
          <section class="allExperience">
            <h3>Experience</h3>
          </section>
        </div>
</template>

<script>

export default {

  mounted() {
    const allEducation = document.getElementsByClassName("allEducation")[0];
    allEducation.innerHTML = "";
    const allExperience = document.getElementsByClassName("allExperience")[0];
    allExperience.innerHTML = "";
    this.getEducation("https://rperry.github.io/CS601_HW5_Perry/college_degrees.json");
    this.getExperience("https://rperry.github.io/CS601_HW5_Perry/experience.json")
  },

  name: "educationExperience",
  data() {
    return {
    };
  },

  methods: {
    // gettting Education text from JSON
    getEducation: async function getEd(url) {
      await fetch(url, {
          method: 'GET'
      })
      .then((response) => this.check(response))
      .then((data) => this.addEducation(data))
      .catch(e => {
          console.log(e);
      });
    },

    // gettting Experience text from JSON
    getExperience: async function getEx(url) {
      await fetch(url, {
          method: 'GET'
      })
      .then((response) => this.check(response))
      .then((data) => this.addExperience(data))
      .catch(e => {
          console.log(e);
      });
    },
    
    check: function checkStatus(getResponse) {
      if (getResponse.status != 200) {
          this.statusCodeException(getResponse.status);
      } else {
          return getResponse.json()
      }
    },

    statusCode: function statusCodeException(code) {
      let message = `Sorry, there seems to be an error. Status code: ${code}`;
      console.log(message);
      return message;
    },

    // iterating through JSON  object and creating a div element for each degree and adding it the DOM
    addEducation: function addEdToPage(data) {
    for (let i= 0; i<= data.college_degrees.length; i++) {
      const educationSection = document.createElement("div");
      educationSection.className = "educationItem";
      
      let edInfo = {};

      for (const [key, value] of Object.entries(data.college_degrees[i].degree)) {
        edInfo[key] = value;
      }

      const sectionContents = 
      `
      <p class="bold">${edInfo.school}</p>
      <p>${edInfo.type}, ${edInfo.major} ${edInfo.year_conferred}</p>
      `;

      educationSection.innerHTML = sectionContents;
      const allEd = document.getElementsByClassName("allEducation")[0];
      allEd.appendChild(educationSection);
    }
  },
  
      // iterating through JSON  object and creating a div element for each job and adding it the DOM
  addExperience: function addExToPage(data) {
    for (let i= 0; i<= data.allExperience.length; i++) {
      const experienceSection = document.createElement("div");
      experienceSection.className = "experienceItem";
      
      let exInfo = {};

      for (const [key, value] of Object.entries(data.allExperience[i].experience)) {
        exInfo[key] = value;
      }

      const sectionContents = 
      `
      <p class="bold">${exInfo.company}</p>
      <p>${exInfo.title}, ${exInfo.location}</p>
      <p>${exInfo.year_start} - ${exInfo.year_end}</p>
      `;

      experienceSection.innerHTML = sectionContents;
      const allEx = document.getElementsByClassName("allExperience")[0];
      allEx.appendChild(experienceSection);
    }
  },

    
  }

}

</script>

<style>

   .edExContainer {

    display: grid;
    grid-template-columns: 0.5fr 1fr 1fr 0.5fr;
    grid-template-rows: .25 1fr 1fr;
    gap: 5px 5px;
    grid-auto-flow: row;
    grid-template-areas:
      ". edExHeader edExHeader ."
      ". edArea edArea ."
      ". exArea exArea .";
    width: 99vw;
    height: 98vh;

  }

  .edExHeader {
    grid-area: edExHeader;
    margin-top: 7%
  }

  .allEducation { 
    grid-area: edArea;
    padding-bottom: 20px;
    border-bottom: 1px solid black;
    align-items: baseline;
  }

  .allExperience { 
    grid-area: exArea; 
    padding-top: 50px;
    padding-bottom: 20px;
  }


  .educationItem {
    padding: 10px;
  }

  .experienceItem {
    padding: 10px;
  }


  .bold {
    font-weight: bold;
  }
   
</style>